// app/components/navbar.jsx
"use client";

import { useSession, signOut } from "next-auth/react";
import Link from "next/link";
// ‚ú® ÁßªÈô§ÈÄôË°åÔºöimport { useEffect, useState } from "react";
// ‚ú® ÁßªÈô§ÈÄôË°åÔºöimport useUser from "@/hooks/useUser"; // ‰∏çÂÜçÈúÄË¶Å useUser hook

import NotifyButton from "@/app/components/notifyButton"; // ÈÄöÁü•ÊåâÈàï‰øùÊåÅ‰∏çËÆä

export default function Navbar() {
    // Áõ¥Êé•Âæû useSession Áç≤Âèñ session Êï∏ÊìöÂíåÁãÄÊÖã
    const { data: session, status } = useSession(); // status: "loading" | "authenticated" | "unauthenticated"

    // ‚ú® ÁßªÈô§ÊâÄÊúâËàá useUser Âíå sessionStorage Áõ∏ÈóúÁöÑ useEffect Âíå state
    // const { user, setUser } = useUser();
    // const [isLogin, setIsLogin] = useState(...)
    // useEffect(() => { ... }, [session, status]);

    const getRoleLinks = () => {
        // Áõ¥Êé•Âæû session.user Áç≤ÂèñËßíËâ≤Ôºå‰∏¶ÈÄ≤Ë°åÂÆâÂÖ®Ê™¢Êü•
        const userRole = session?.user?.role;

        if (!userRole) return []; // Â¶ÇÊûúÊ≤íÊúâËßíËâ≤Ôºà‰æãÂ¶ÇÊú™ÁôªÂÖ•Êàñ‰ªçÂú®ËºâÂÖ•ÔºâÔºåËøîÂõûÁ©∫Èô£Âàó

        // Ê†πÊìöËßíËâ≤ËøîÂõûÂ∞çÊáâÁöÑÂ∞éË¶ΩÈÄ£Áµê
        switch (userRole) {
            case "CUSTOMER":
                return [
                    { href: "/menu", name: "ËèúÂñÆ" },
                    { href: "/orders", name: "ÊàëÁöÑË®ÇÂñÆ" },
                ];
            case "STAFF":
                return [
                    { href: "/orders/pending", name: "Á≠âÂæÖ‰∏≠ÁöÑË®ÇÂñÆ" },
                    { href: "/orders/ready", name: "ÂÆåÊàêÁöÑË®ÇÂñÆ" },
                ];
            case "CHEF":
                return [{ href: "/kitchen", name: "ÂªöÊàøË®ÇÂñÆ" }];
            case "OWNER":
                return [
                    { href: "/admin/menu", name: "ËèúÂñÆÁÆ°ÁêÜ" },
                    { href: "/admin/users", name: "‰ΩøÁî®ËÄÖÁÆ°ÁêÜ" },
                    { href: "/admin/orders/pending", name: "Á≠âÂæÖ‰∏≠ÁöÑË®ÇÂñÆ" },
                    { href: "/admin/orders/completed", name: "ÂÆåÊàêÁöÑË®ÇÂñÆ" },
                    { href: "/admin/orders/kitchen", name: "ÂªöÊàøË®ÇÂñÆ" },
                ];
            default:
                return [];
        }
    };

    const handelSignOut = () => {
        // Âè™ÈúÄË¶ÅË™øÁî® signOut() Âç≥ÂèØÔºåNextAuth.js ÊúÉËôïÁêÜ session ÁöÑÊ∏ÖÈô§
        signOut({ callbackUrl: "/login" }); // ÁôªÂá∫ÂæåË∑≥ËΩâÂà∞ÁôªÂÖ•È†Å
        // ‚ú® ÁßªÈô§ÊâãÂãïÊ∏ÖÈô§ sessionStorage Âíå setUser ÁöÑÈÇèËºØ
        // sessionStorage.removeItem("user");
        // setUser(null);
        // setIsLogin(false);
    };

    // Âà§Êñ∑ÊòØÂê¶Â∑≤ÁôªÂÖ•ÂíåËºâÂÖ•ÁãÄÊÖã
    const isAuthenticated = status === "authenticated";
    const isLoading = status === "loading";

    return (
        <nav className="sticky top-0 z-50 bg-gradient-to-r from-orange-400 via-pink-500 to-red-500 text-white shadow-md">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-3 flex justify-between items-center overflow-x-auto">
                <Link
                    href="/"
                    className="text-2xl font-bold tracking-wide hover:opacity-90 transition-opacity duration-300"
                    aria-label="ÂâçÂæÄÈ¶ñÈ†Å"
                >
                    üçΩ Á∂≤Ë∑ØÊó©È§êË®ÇÈ§êÁ≥ªÁµ±
                </Link>

                <div className="flex flex-wrap items-center gap-4">
                    {isLoading ? ( // Â¶ÇÊûúÊ≠£Âú®ËºâÂÖ• sessionÔºåÈ°ØÁ§∫ËºâÂÖ•Ë®äÊÅØ
                        <span className="text-sm">ËºâÂÖ•‰∏≠...</span>
                    ) : isAuthenticated ? ( // Â¶ÇÊûúÂ∑≤Ë™çË≠âÔºàÁôªÂÖ•Ôºâ
                        <>
                            {getRoleLinks().map((link) => (
                                <Link
                                    key={link.href}
                                    href={link.href}
                                    className="text-white font-medium hover:underline hover:text-yellow-200 transition duration-300"
                                    aria-label={link.name}
                                >
                                    {link.name}
                                </Link>
                            ))}

                            <span className="hidden sm:inline-block font-semibold">
                                {/* ‚ú® ‰ΩøÁî®ÂèØÈÅ∏Èèà‰æÜÂÆâÂÖ®Ë®™Âïè user.name Êàñ user.email */}
                                ÊÇ®Â•ΩÔºå{session?.user?.name || session?.user?.email}
                            </span>

                            {/* ‚ú® ÈóúÈçµ‰øÆÊîπÔºöÂè™ÊúâÁï∂ session.user.id Â≠òÂú®ÊôÇÊâçÊ∏≤Êüì NotifyButton ‚ú® */}
                            {session.user?.id && <NotifyButton userId={session.user.id} />}

                            <button
                                onClick={handelSignOut}
                                className="bg-white text-pink-600 font-semibold px-3 py-1.5 rounded-md hover:bg-gray-100 transition duration-300"
                                aria-label="ÁôªÂá∫Â∏≥Ëôü"
                            >
                                ÁôªÂá∫
                            </button>
                        </>
                    ) : ( // Â¶ÇÊûúÊú™Ë™çË≠âÔºàÊú™ÁôªÂÖ•Ôºâ
                        <Link
                            href="/login"
                            className="bg-white text-pink-600 font-semibold px-4 py-1.5 rounded-md hover:bg-gray-100 transition duration-300"
                            aria-label="ÁôªÂÖ•Â∏≥Ëôü"
                        >
                            ÁôªÂÖ•
                        </Link>
                    )}
                </div>
            </div>
        </nav>
    );
}