"use server"; // ‚ú® ÂøÖÈ†àÊúâÈÄôË°åÔºåÂ∞áÂÖ∂Ê®ôË®òÁÇ∫ Server Actions

import { prisma } from '@/lib/prisma'; // ‚ú® Â∞éÂÖ•ÂÖ∑ÂêçÂ∞éÂá∫ÁöÑ prisma ÂÆ¢Êà∂Á´Ø

export const addNotification = async (body, userId) => {
    try {
        // ‚ú® ÈÄôË£°ÊáâË©≤ÊòØÊ∑ªÂä†ÈÄöÁü•Âà∞Ë≥áÊñôÂ∫´ÁöÑÈÇèËºØ
        if (!userId || !body || !body.message || !body.orderId || !body.title || !body.type) {
            throw new Error("Missing notification data or userId.");
        }
        const newNotification = await prisma.notification.create({
            data: {
                userId: userId,
                orderId: body.orderId, // ÂÅáË®≠ body ÂåÖÂê´ orderId
                message: body.message,
                // ÂÖ∂‰ªñÊ¨Ñ‰ΩçÂ¶Ç isRead ÊúÉÊúâ default ÂÄº
                // title Âíå type ÈúÄË¶ÅÂú®‰Ω†ÁöÑ Notification model ‰∏≠ÂÆöÁæ©ÊàñËôïÁêÜ
            }
        });
        return { success: true, notification: newNotification };
    } catch (error) {
        console.error("Failed to add notification (Server Action):", error);
        return { error: error.message };
    }
};

export const getUserNotification = async (userId) => {
    try {
        // ‚ú® ÈÄôË£°ÊáâË©≤ÊòØÂæûË≥áÊñôÂ∫´Áç≤Âèñ‰ΩøÁî®ËÄÖÈÄöÁü•ÁöÑÈÇèËºØ
        if (!userId) {
            throw new Error("User ID is required for fetching notifications.");
        }
        const notifications = await prisma.notification.findMany({
            where: { userId: userId },
            orderBy: { createdAt: 'desc' }, // ÊåâÊúÄÊñ∞ÊéíÂ∫è
        });
        return notifications;
    } catch (error) {
        console.error("Failed to get user notifications (Server Action):", error);
        return { error: error.message }; // ËøîÂõûÈåØË™§Ë®äÊÅØÔºå‰ª•‰æøÂâçÁ´ØËôïÁêÜ
    }
};

export const deleteNotification = async (notificationId) => {
    try {
        // ‚ú® ÈÄôË£°ÊáâË©≤ÊòØÂæûË≥áÊñôÂ∫´Âà™Èô§ÈÄöÁü•ÁöÑÈÇèËºØ
        if (!notificationId) {
            throw new Error("Notification ID is required for deleting.");
        }
        await prisma.notification.delete({
            where: { id: notificationId },
        });
        return { success: true };
    } catch (error) {
        console.error("Failed to delete notification (Server Action):", error);
        // Â¶ÇÊûúÊòØÊâæ‰∏çÂà∞ÈÄöÁü•ÁöÑÈåØË™§ÔºåÂèØ‰ª•Êõ¥Á≤æÁ¢∫Âú∞ËôïÁêÜ
        if (error.code === 'P2025') {
            return { error: "Notification not found." };
        }
        return { error: error.message };
    }
};